<template>
  <li @click="$router.push('/playlist?id=' + item.id)" class="card-item">
    <div class="thumb">
      <img
        class="bg"
        :src="`${item.picUrl}?imageView=1&type=webp&thumbnail=247x0`"
      />
      <span
        ><img
          style="width: 10px"
          src="data:image/svg+xml;base64,"
          alt=""
        />{{ item.playCount | parseCount }}</span
      >
    </div>
    <p>{{ item.name }}</p>
  </li>
</template>

<script>
export default {
  props: {
    item: {
      type: Object,
      required: true,
    },
  },
  filters: {
    parseCount: function (val) {
      if (val > 100000000) {
        return (val / 100000000).toFixed(1) + "";
      } else if (val > 10000) {
        return (val / 10000).toFixed(1) + "";
      } else {
        return val;
      }
    },
  },
};
</script>

<style lang="less" scoped>
.card-item {
  flex-basis: 25%;
  flex-grow: 1;
  &:nth-child(3n + 2) {
    margin: 0 3px;
  }
  .thumb {
    position: relative;
    // top: 0;
    // left: 0;
    img.bg {
      vertical-align: middle;
    }
    span {
      position: absolute;
      top: 5px;
      right: 5px;
      font-size: 10px;
      color: #fff;
    }
  }
  p {
    font-size: 13px;
    // padding:15px 5px;
    margin: 3px;
    margin-bottom: 12px;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
    text-overflow: ellipsis;
    line-height: 1.2;
    // height: calc((13px * 2) + 10px);
  }
}
</style>